html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: #FFFFFF;
  font-size: 1.6rem;
  font-optical-sizing: auto;
  font-style: normal;
  line-height: 1.8;
  font-family: "Montserrat", serif;
  overflow-x: hidden;
  position: relative;
}
body.overflow-hidden {
  overflow: hidden;
}

p {
  color: #0D0D0D;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  text-align: center;
  color: #0D0D0D;
  text-transform: capitalize;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}

blockquote {
  font-family: "Courgette", serif;
}

.contenedor {
  width: min(95%, 120rem);
  margin-inline: auto;
}

input[type=submit] {
  padding: 1.2rem 3.2rem;
}

.whatsapp-button {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 1;
  background-color: #25d366;
  padding: 1rem;
  height: 5rem;
  width: 5rem;
  box-shadow: rgba(0, 0, 0, 0.2);
  font-size: 3rem;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: transform 0.3s linear, background-color 0.3s ease;
}
.whatsapp-button:hover {
  transform: scale(1.1);
  background-color: rgb(66.8016129032, 221.9983870968, 124.7774193548);
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.text-dark {
  color: #0D0D0D;
}

.enlace {
  font-weight: 700;
}

.text-white {
  color: #FFFFFF;
}

.uppercase {
  text-transform: uppercase;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-sm-100 {
  width: 100%;
}
@media (min-width: 768px) {
  .w-sm-100 {
    width: auto;
  }
}

.centrar-texto {
  text-align: center;
}

.justificar-texto {
  text-align: justify;
}

.contenido-centrado {
  width: min(95%, 80rem);
  margin-inline: auto;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.fade-Out {
  opacity: 0;
  transition: opacity 6s ease-out;
}

.boton-rojo {
  background-color: #C21E24;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-decoration: none;
  padding: 0.8rem 2.5rem;
  outline: none;
  text-align: center;
  display: inline-block;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 2rem;
}
.boton-rojo:hover {
  background-color: rgb(216.0848214286, 33.4151785714, 40.0982142857);
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #C21E24;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-decoration: none;
  padding: 0.8rem 2.5rem;
  outline: none;
  text-align: center;
  display: block;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 2rem;
}
.boton-rojo-block:hover {
  background-color: rgb(216.0848214286, 33.4151785714, 40.0982142857);
  cursor: pointer;
}

.boton-negro {
  background-color: #0D0D0D;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-decoration: none;
  padding: 0.8rem 2.5rem;
  outline: none;
  text-align: center;
  display: inline-block;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 2rem;
}
.boton-negro:hover {
  background-color: rgb(25.75, 25.75, 25.75);
  cursor: pointer;
}

.boton-negro-block {
  background-color: #0D0D0D;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-decoration: none;
  padding: 0.8rem 2.5rem;
  outline: none;
  text-align: center;
  display: block;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 2rem;
}
.boton-negro-block:hover {
  background-color: rgb(25.75, 25.75, 25.75);
  cursor: pointer;
}

.boton-blanco {
  background-color: #FFFFFF;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-decoration: none;
  padding: 0.8rem 2.5rem;
  outline: none;
  text-align: center;
  display: inline-block;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 2rem;
}
.boton-blanco:hover {
  background-color: hsl(0, 0%, 105%);
  cursor: pointer;
}

.boton-blanco-block {
  background-color: #FFFFFF;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-decoration: none;
  padding: 0.8rem 2.5rem;
  outline: none;
  text-align: center;
  display: block;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 2rem;
}
.boton-blanco-block:hover {
  background-color: hsl(0, 0%, 105%);
  cursor: pointer;
}

.header {
  background-color: #0D0D0D;
  color: #FFFFFF;
}

.header-home {
  background-color: transparent;
  padding: 0;
  position: relative;
  overflow: hidden;
}
.header-home video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  display: block;
}
.header-home .overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(13, 13, 13, 0.6);
  height: 100%;
}
.header-home .contenido-video {
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}
.header-home .contenido-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 8vh;
}
.header-home .contenido-header h2 {
  color: #FFFFFF;
  font-size: clamp(2.4rem, 4vw, 3.5rem);
  text-transform: capitalize;
}
.header-home .contenido-header h2 span {
  font-weight: 400;
  display: block;
  font-size: clamp(1.8rem, 4vw, 2.6rem);
}
.header-home .contenido-header .mas-informacion {
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header-home .contenido-header .mas-informacion {
    padding-top: 16rem;
  }
}
@media (min-width: 768px) {
  .header-home .contenido-header .mas-informacion {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .header-home .contenido-header .mas-informacion .boton-negro {
    align-self: flex-start;
    margin-top: 0;
  }
}
.header-home .frase {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header-home .frase {
    max-width: 50rem;
    margin: 0;
  }
}
.header-home .frase blockquote,
.header-home .frase p {
  margin: 0;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header-home .frase blockquote,
  .header-home .frase p {
    text-align: right;
  }
}
.header-home .frase blockquote {
  font-family: "Courgette", serif;
  font-size: clamp(1.4rem, 4vw, 2rem);
}
.header-home .frase p {
  font-size: 2rem;
  font-style: italic;
}

.barra {
  padding: 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.barra .logo-soarco {
  order: 1;
}
.barra .logo-macondo {
  order: 2;
}
.barra .logo-macondo,
.barra .logo-soarco {
  width: 15rem;
  align-self: center;
}
@media (min-width: 768px) {
  .barra .logo-macondo,
  .barra .logo-soarco {
    align-self: flex-end;
  }
}

.navegacion-principal {
  opacity: 0;
  visibility: hidden;
  max-height: 0;
  transition-delay: 100ms, 300ms, 300ms;
  transition-property: visibility, opacity, max-height;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  order: 4;
}
@media (min-width: 768px) {
  .navegacion-principal {
    all: unset;
    display: flex;
    order: 2;
  }
}
.navegacion-principal a {
  display: block;
  color: #FFFFFF;
  text-align: center;
  padding: 0.5rem 0.6rem;
  font-size: clamp(1.6rem, 4vw + 1rem, 2rem);
  font-weight: 700;
  transition: color 0.3s ease;
}
.navegacion-principal a:hover {
  color: rgb(199.5, 199.5, 199.5);
}

.menu-movil {
  display: flex;
  align-items: center;
  justify-content: center;
  order: 3;
}
@media (min-width: 768px) {
  .menu-movil {
    all: unset;
  }
}
.menu-movil:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .menu-movil {
    display: none;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  max-height: 35rem;
  transition-delay: 100ms, 300ms, 300ms;
  transition-property: max-height, visibility, opacity;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
}

.footer {
  background-color: #0D0D0D;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer h3 {
  color: #FFFFFF;
  margin-bottom: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer h3 {
    text-align: left;
  }
}
.footer .navegacion-footer {
  margin-bottom: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .navegacion-footer {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.footer-menu nav {
  display: flex;
  flex-direction: column;
}
.footer-menu nav a {
  color: #FFFFFF;
  font-size: 1.8rem;
  text-align: center;
  transition: color 0.3s ease;
}
@media (min-width: 768px) {
  .footer-menu nav a {
    text-align: left;
  }
}
.footer-menu nav a:hover {
  color: rgb(199.5, 199.5, 199.5);
}

.logo-fiduoccidente {
  background-color: #FFFFFF;
  padding: 1rem;
  width: 12rem;
  height: auto;
}
@media (min-width: 768px) {
  .logo-fiduoccidente {
    width: 18rem;
    justify-self: flex-end;
  }
}

.contenido-footer {
  width: min(95%, 120rem);
  margin-inline: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 0.5px solid #FFFFFF;
}
@media (min-width: 768px) {
  .contenido-footer {
    flex-direction: row;
    justify-content: space-between;
  }
}
.contenido-footer .copyright {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .contenido-footer .copyright {
    text-align: left;
    font-size: 1.8rem;
  }
}

.redes-sociales {
  display: flex;
  gap: 2rem;
}
@media (min-width: 768px) {
  .redes-sociales {
    margin-top: 0;
  }
}
.redes-sociales a {
  color: #FFFFFF;
  font-size: 1.8rem;
  transition: color 0.3s ease;
}
.redes-sociales a:hover {
  color: #aeaeae;
}
.redes-sociales a::before {
  font-family: "Font Awesome 6 Brands";
}
.redes-sociales a span {
  display: none;
}
.redes-sociales a[href="https://www.facebook.com/"]::before {
  content: "\f39e";
}
.redes-sociales a[href="https://www.instagram.com/"]::before {
  content: "\f16d";
}
.redes-sociales a[href="https://www.x.com/"]::before {
  content: "\f099";
}
.redes-sociales a[href="https://www.tiktok.com/"]::before {
  content: "\e07b";
}
.redes-sociales a[href="https://www.youtube.com/"]::before {
  content: "\f167";
}

.proximos-lanzamientos {
  background-color: #0D0D0D;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .proximos-lanzamientos {
    position: sticky;
    top: 0;
    left: 0;
  }
}
.proximos-lanzamientos p {
  color: #FFFFFF;
  font-size: 1.6rem;
  margin: 0;
  font-weight: 700;
  font-style: italic;
}
.proximos-lanzamientos span {
  font-style: normal;
  font-weight: 400;
}

.proyectos h1 {
  margin-bottom: 2rem;
}

.contenedor-proyectos {
  display: grid;
  grid-template-rows: repeat(auto-fit, minmax(25rem, 1fr));
  gap: 2rem;
}
@media (min-width: 768px) {
  .contenedor-proyectos {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}

.proyecto {
  position: relative;
  transition: transform 0.3s ease-in-out;
  overflow: hidden;
}
.proyecto .overlay {
  position: absolute;
  background-color: rgba(13, 13, 13, 0.6);
  inset: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.proyecto:hover img {
  transform: scale(1.1);
}
.proyecto img {
  height: 50vh;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .proyecto img {
    height: 70vh;
  }
}

.proyecto-informacion {
  padding: 2rem;
}
.proyecto-informacion h3 {
  color: #FFFFFF;
  font-size: clamp(2.5rem, 4vw, 3.5rem);
}
.proyecto-informacion p {
  font-weight: 700;
  color: #E1E1E1;
  font-size: clamp(2rem, 4vw, 2.8rem);
}

.macondo {
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.macondo h1 {
  text-align: center;
  font-size: clamp(2rem, 4vw + 1rem, 6rem);
  color: transparent;
  -webkit-text-stroke: 1px #FFFFFF;
  text-shadow: 0 0 10px #FFFFFF;
  transition: all 0.5s ease;
}
.macondo h1:hover {
  color: #FFFFFF;
  -webkit-text-stroke: unset;
  text-shadow: none;
}

.ml2 {
  font-weight: 900;
  font-size: 3.5em;
}
.ml2 .letter {
  display: inline-block;
  line-height: 1em;
}

.testimoniales h1,
.testimoniales h2 {
  text-transform: capitalize;
}

@media (min-width: 768px) {
  .contenedor-testimonios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.testimonio {
  margin-top: 2rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #E1E1E1;
  border-radius: 1rem;
}
.testimonio blockquote {
  margin: 1rem 0;
}
.testimonio .imagen-testimonio {
  margin-top: 2rem;
  height: 15rem;
  width: 15rem;
