@use 'sass:color';
@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    padding: 2rem;
    display: flex;
    flex-direction: column;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }

    .logo-soarco {
        order: 1;
    }

    .logo-macondo {
        order: 2;
    }


    .logo-macondo,
    .logo-soarco {
        width: 15rem;
        align-self: center;

        @include m.tablet {
            align-self: flex-end;
        }
    }

}

.navegacion-principal {
    opacity: 0;
    visibility: hidden;
    max-height: 0;
    transition-delay: 100ms, 300ms, 300ms;
    transition-property: visibility, opacity, max-height;
    transition-timing-function: ease-in-out;
    transition-duration: 300ms;
    order: 4;

    @include m.tablet {
        all: unset;
        display: flex;
        order: 2;
    }

    a {
        display: block;
        color: v.$blanco;
        text-align: center;
        padding: .5rem .6rem;
        font-size: clamp(1.6rem, 4vw + 1rem, 2rem);
        font-weight: v.$bold;
        transition: color .3s ease;

        &:hover {
            color: color.adjust(v.$gris, $lightness: -10%);
        }
    }
}

.menu-movil {
    display: flex;
    align-items: center;
    justify-content: center;
    order: 3;

    @include m.tablet {
        all: unset;
    }

    &:hover {
        cursor: pointer;
    }

    @include m.tablet {
        display: none;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    max-height: 35rem;
    transition-delay: 100ms, 300ms, 300ms;
    transition-property: max-height, visibility, opacity;
    transition-timing-function: ease-in-out;
    transition-duration: 300ms;
}