@use 'base/variables' as v;
@use 'base/mixins' as m;

.proyectos {
    h1 {
        margin-bottom: 2rem;
    }
}

.contenedor-proyectos {
    display: grid;
    grid-template-rows: repeat(auto-fit, minmax(25rem, 1fr));
    gap: 2rem;

    @include m.tablet {
        grid-template-rows: unset;
        grid-template-columns: repeat(2, 1fr);
    }
}

.proyecto {
    position: relative;
    transition: transform .3s ease-in-out;
    overflow: hidden;

    .overlay {
        position: absolute;
        background-color: v.$transparencia1;
        inset: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &:hover {

        img {
            transform: scale(1.1);
        }
    }



    img {
        height: 50vh;
        transition: transform .3s ease-in-out;

        @include m.tablet {
            height: 70vh;

        }

    }
}

.proyecto-informacion {
    padding: 2rem;

    h3 {
        color: v.$blanco;
        font-size: clamp(2.5rem, 4vw, 3.5rem);
    }

    p {
        font-weight: v.$bold;
        color: v.$gris;
        font-size: clamp(2rem, 4vw, 2.8rem);
    }

}

.macondo {
    height: 100vh;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;

    h1 {
        text-align: center;
        font-size: clamp(2rem, 4vw + 1rem, 6rem);
        color: transparent;
        -webkit-text-stroke: 1px v.$blanco;
        text-shadow: 0 0 10px v.$blanco;
        transition: all .5s ease;

        &:hover {
            color: v.$blanco;
            -webkit-text-stroke: unset;
            text-shadow: none;
        }
    }


}



.ml2 {
    font-weight: 900;
    font-size: 3.5em;

    .letter {
        display: inline-block;
        line-height: 1em
    }
}