@use 'sass:color';
@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$negro;
    padding: calc(v.$separacion / 2) 0;
    margin-bottom: 0;

    h3 {
        color: v.$blanco;
        margin-bottom: 2rem;
        text-align: center;

        @include m.tablet {
            text-align: left;
        }
    }

    .navegacion-footer {
        margin-bottom: calc(v.$separacion / 2);
        display: flex;
        flex-direction: column;
        gap: 2rem;
        align-items: center;

        @include m.tablet {
            flex-direction: row;
            align-items: flex-start;
            justify-content: space-between;
        }
    }
}

.footer-menu {
    nav {
        display: flex;
        flex-direction: column;

        a {
            color: v.$blanco;
            font-size: 1.8rem;
            text-align: center;
            transition: color .3s ease;

            @include m.tablet {
                text-align: left;
            }

            &:hover {
                color: color.adjust(v.$gris, $lightness: -10%);
            }
        }
    }

}

.logo-fiduoccidente {
    background-color: v.$blanco;
    padding: 1rem;
    width: 12rem;
    height: auto;

    @include m.tablet {
        width: 18rem;
        justify-self: flex-end;
    }

}

.contenido-footer {
    @include m.contenedor;

    display: flex;
    flex-direction: column;
    align-items: center;
    border-top: .5px solid v.$blanco;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }

    .copyright {
        color: v.$blanco;
        text-align: center;
        font-size: 1.4rem;

        @include m.tablet {
            text-align: left;
            font-size: 1.8rem;

        }

    }
}

.redes-sociales {
    display: flex;
    gap: 2rem;

    @include m.tablet {
        margin-top: 0;
    }

    a {
        color: v.$blanco;
        font-size: 1.8rem;
        transition: color .3s ease;

        &:hover {
            color: color.adjust(v.$gris, $lightness: -20%);
        }

        &::before {
            font-family: 'Font Awesome 6 Brands';
        }

        span {
            display: none;
        }

        &[href="https://www.facebook.com/"]::before {
            content: "\f39e";
        }

        &[href="https://www.instagram.com/"]::before {
            content: "\f16d";
        }

        &[href="https://www.x.com/"]::before {
            content: "\f099";
        }

        &[href="https://www.tiktok.com/"]::before {
            content: "\e07b";
        }

        &[href="https://www.youtube.com/"]::before {
            content: "\f167";
        }
    }
}