@use 'base/variables' as v;
@use 'base/mixins' as m;

.proximos-lanzamientos {
    background-color: v.$negro;
    padding: 1rem 0;

    @include m.tablet {
        position: sticky;
        top: 0;
        left: 0;
    }

    p {
        color: v.$blanco;
        font-size: 1.6rem;
        margin: 0;
        font-weight: v.$bold;
        font-style: italic;
    }

    span {
        font-style: normal;
        font-weight: v.$regular;
    }

}